.header {
  padding-top: 53px;
}

.header__inner {
  display: flex;
  align-items: center;
}

.header-items {
  margin-right: 117px;
  display: flex;
}

.header-item {
  font-weight: 300;
  font-size: 18px;
  line-height: 26px;
  padding-left: 42px;
  position: relative;
  margin-right: 91px;
  max-width: 241px;

  &::before {
    content: '';
    position: absolute;
    top: 3px;
    left: 0;
    width: 8px;
    height: 8px;
    background: #FFFFFF;
    border-radius: 50%;
    border: 6px solid #F7A723;
    box-shadow: 0px 0px 30px #F7A723;
  }

  &:last-child {
    margin-right: 0;
  }
}

.hero {
  padding-top: 69px;
  padding-bottom: 50px;
}

.hero__inner {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  position: relative;

  &::before {
    content: url(../img/hero-text.svg);
    position: absolute;
    left: -237;
    top: 28px;
    z-index: -1;
  }
}

.hero__title {
  font-family: 'Bebas Neue', sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 170px;
  line-height: 170px;
  background: linear-gradient(180deg, #F7A723 0%, #C77C00 75%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;
}

.hero__subtitle {
  font-weight: 300;
  font-size: 36px;
  line-height: 54px;
  margin-top: -13px;
  margin-bottom: 40px;

  span {
    border-bottom: 3px solid #fff;
  }
}

.hero-items {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  max-width: 523px;
  row-gap: 39px;
  margin-bottom: 49px;
}

.hero-item {
  display: flex;
  padding-left: 53px;
  position: relative;
  font-weight: 300;
  font-size: 18px;
  line-height: 22px;

  &::before {
    content: url(../img/lightning.png);
    position: absolute;
    top: -29px;
    left: -29px;
  }
}

.hero__wrapper {
  display: flex;
  align-items: center;
  margin-bottom: 45px;
}

.hero__kz {
  padding: 12px 41px 12px 91px;
  font-weight: 300;
  font-size: 18px;
  line-height: 26px;
  border: 2px solid #338AF3;
  border-radius: 50px;
  max-width: 280px;
  position: relative;
  margin-right: 30px;

  &::before {
    content: url(../img/kz.svg);
    position: absolute;
    top: 0;
    left: -2px;
  }

  span {
    font-weight: 700;
  }
}

.hero__text {
  font-weight: 300;
  font-size: 22px;
  line-height: 28px;
  max-width: 340px;

  span {
    font-weight: 500;
    font-style: italic;
    color: #F7A723;
  }
}

.hero-form {
  padding: 28px 30px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 606px;
  position: relative;

  &::before {
    content: '';
    height: 100%;
    width: 100%;
    border: 2px solid #F7A723;
    position: absolute;
    top: 0;
    left: 0;
    clip-path: polygon(0% 0%, 100% 0%, 100% 90%, 94% 100%, 0% 100%);
    z-index: -1;
    box-sizing: border-box;
  }

  &::after {
    content: '';
    position: absolute;
    height: 2px;
    width: 50px;
    background-color: #F7A723;
    transform: rotate(-42deg);
    bottom: 16px;
    right: -6px;
  }
}

.hero-form__title {
  font-weight: 300;
  font-size: 24px;
  line-height: 28px;
  margin-bottom: 29px;
}

.hero-form-prices {
  display: flex;
  align-items: center;
  margin-bottom: 38px;
}

.hero-form-price__old {
  font-weight: 300;
  font-size: 32px;
  line-height: 28px;
  text-decoration-line: line-through;
  color: #929292;

  &::after {
    content: url(../img/price-arrow.svg);
    margin: 0 50px;
  }
}

.hero-form-price__new {
  font-size: 36px;
  line-height: 28px;
  color: #F7A723;
}

.hero-form-inputs {
  display: flex;
  gap: 20px;
  margin-bottom: 20px;

  .input {
    width: 50%;
  }
}

.input {
  padding: 21px 23px;
  background: #FFFFFF;
  font-weight: 300;
  font-size: 18px;
  line-height: 28px;
  border: 0;

  &::placeholder {
    color: #6D6D6D;
  }
}

.hero-form__btn {
  width: 100%;
}

.hero__bg {
  display: flex;
  position: absolute;
  left: 640px;
  bottom: 0;
  z-index: -1;
}

.hero__img {
  display: flex;
  position: absolute;
  left: 560px;
  bottom: -38px;

  img:last-child {
    display: none;
  }
}

@media (max-width: 1054px) {
  .header {
    padding-top: 44px;
  }

  .header__inner {
    flex-direction: column;
  }

  .header-items {
    margin-right: 0;
    flex-direction: column;
    margin-bottom: 55px;
  }

  .header-item {
    max-width: 100%;
    margin-right: 0;
    margin-bottom: 19px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  .hero {
    padding-top: 79px;
  }

  .hero__title {
    font-size: 150px;
    line-height: 130px;
    text-align: center;
    margin-bottom: 15px;
  }

  .hero__inner {
    align-items: center;
  }

  .hero__subtitle {
    margin-top: 0;
    font-size: 30px;
    line-height: 46px;
    text-align: center;
    margin-bottom: 9px;
  }

  .hero__img {
    position: static;
    width: 522px;

    img:first-child {
      display: none;
    }

    img:last-child {
      display: block;
      width: 100%;
    }
  }

  .hero-items {
    max-width: unset;
    row-gap: 30px;
    margin-bottom: 41px;
    flex-direction: column;
    margin-top: -20px;
  }

  .hero__wrapper {
    margin-bottom: 42px;
    flex-direction: column;
  }

  .hero__kz {
    margin-right: 0;
    margin-bottom: 35px;
  }

  .hero__text {
    font-size: 24px;
    line-height: 34px;
    text-align: center;
    max-width: 380px;
  }

  .hero-form {
    max-width: 420px;
    padding: 32px 30px;

    &::before {
      clip-path: polygon(0% 0%, 100% 0%, 100% 91.6%, 93.3% 100%, 0% 100%);
    }

    &:after {
      width: 48px;
      bottom: 18px;
      right: -9px;
      transform: rotate(-52deg);
    }
  }

  .hero-form__title {
    margin-bottom: 25px;
    font-size: 28px;
    line-height: 34px;
  }

  .hero-form-price__old {
    font-size: 30px;
    line-height: 28px;
  }

  .hero-form-price__new {
    font-size: 34px;
    line-height: 28px;
  }

  .hero-form-price__old:after {
    margin: 0px 17px;
  }

  .hero-form-prices {
    margin-bottom: 28px;
  }

  .hero-form-inputs {
    flex-direction: column;
    width: 100%;

    .input {
      width: 100%;
    }
  }

  .hero__bg {
    position: static;
    width: 894px;
    margin-left: 110px;
    margin-top: 41px;

    img {
      width: 100%;
    }
  }

  .hero__inner:before {
    top: -152px;
    left: -157px;
  }
}