@import "../../node_modules/normalize.css/normalize.css";
@import "./components/fonts";
@import "./components/index";

* {
  box-sizing: border-box;
}

body {
  font-family: 'Inter', sans-serif;
  background: #1F242B;
  color: #FFFFFF;
}

.page {
  overflow: hidden;
  min-width: 480px;
}

p {
  margin: 0;
}

h1 {
  margin: 0;
}

a {
  text-decoration: none;
}

.container {
  padding-left: 15px;
  padding-right: 15px;
  width: 100%;
  max-width: 1390px;
  margin: 0 auto;
}

.btn.btn--light {
  font-weight: 500;
  font-size: 18px;
  line-height: 26px;
  color: #1F242B;
  background: #FFFFFF;
  padding: 17px 59px;
  clip-path: polygon(0% 0%, 100% 0%, 93.9% 100%, 0% 100%);
  transition-duration: 0.3s;
  transition-property: background-color, color;

  &:hover {
    background: #F7A723;
    color: #ffffff;
  }
}

.btn.btn--primary {
  font-weight: 500;
  font-size: 22px;
  line-height: 28px;
  color: #FFFFFF;
  background: #F7A723;
  padding: 21px;
  border: 0;
  outline: none;
  transition-duration: 0.3s;
  transition-property: background-color, color;
  cursor: pointer;
  clip-path: polygon(0% 0%, 100% 0%, 100% 72%, 96% 100%, 0% 100%);

  &:hover {
    background: darken(#F7A723, 10%);
  }
}

@media (max-width: 1054px) {
  .container {
    padding-left: 30px;
    padding-right: 30px;
  }

  .btn.btn--primary {
    clip-path: polygon(0% 0%, 100% 0%, 100% 71%, 94.7% 100%, 0% 100%);
  }
}